import React, { useState } from 'react';
import { useAuth } from '../contexts/AuthContext';

function Account() {
  const { currentUser } = useAuth();
  const [activeTab, setActiveTab] = useState('info');
  const [favoriteStore, setFavoriteStore] = useState(currentUser.favoriteStore);
  const [paymentMethod, setPaymentMethod] = useState(currentUser.paymentMethod);

  const handleFavoriteStoreChange = (e) => {
    setFavoriteStore(e.target.value);
  };

  const handlePaymentMethodChange = (e) => {
    setPaymentMethod(e.target.value);
  };

  // Dummy purchase history data
  const purchaseHistory = [
    { id: 1, date: '2023-07-20', items: ['寿司セット', '鮭'], total: 3000 },
    { id: 2, date: '2023-07-15', items: ['タコ唐揚げ', 'キスの天ぷら'], total: 1500 },
  ];

  return (
    <div className="account">
      <h2>アカウント</h2>
      <div className="tabs">
        <button onClick={() => setActiveTab('info')} className={activeTab === 'info' ? 'active' : ''}>
          アカウント情報
        </button>
        <button onClick={() => setActiveTab('history')} className={activeTab === 'history' ? 'active' : ''}>
          購入履歴
        </button>
      </div>

      {activeTab === 'info' && (
        <div className="account-info">
          <h3>アカウント情報</h3>
          <p>名前: {currentUser.name}</p>
          <p>メールアドレス: {currentUser.email}</p>
          <p>住所: {currentUser.address}</p>

          <h3>お気に入り店舗</h3>
          <select value={favoriteStore} onChange={handleFavoriteStoreChange}>
            <option value="">選択してください</option>
            <option value="店舗A">店舗A</option>
            <option value="店舗B">店舗B</option>
            <option value="店舗C">店舗C</option>
          </select>

          <h3>支払い方法</h3>
          <select value={paymentMethod} onChange={handlePaymentMethodChange}>
            <option value="">選択してください</option>
            <option value="クレジットカード">クレジットカード</option>
            <option value="PayPay">PayPay</option>
            <option value="店舗支払い">店舗支払い</option>
          </select>
        </div>
      )}

      {activeTab === 'history' && (
        <div className="purchase-history">
          <h3>購入履歴</h3>
          {purchaseHistory.map((purchase) => (
            <div key={purchase.id} className="purchase-item">
              <p>日付: {purchase.date}</p>
              <p>商品: {purchase.items.join(', ')}</p>
              <p>合計: {purchase.total}円</p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default Account;